@import url('https://fonts.googleapis.com/css2?family=Merriweather:wght@900&display=swap');

body{
    font-family: 'Merriweather', serif;
    margin: 0;
    background-color: #111111;
    text-align: center;
    color: white;
    user-select: none;
    padding-top: 10vh;
}
.container{
    display: flex;
    flex-direction: column;
    align-items: center;
    width: 100%;
    background-color: #505a6393;
}
h2{
    font-size: 50px;
    margin: 0;
    text-shadow: 15px 5px 2px rgb(145, 134, 134);
}
h3{
    font-size: 30px;
    margin: 20px;
}